<template>


    <div id="mybaidumap" v-bind:style="mapStyle"></div>



</template>

<script>

export default {
    props: {
        // 
        mapHeight:{
            type:Number,
            default:600
        },
        longitude:{
            type:Number,
            default:116.404
        },
        // 
        latitude:{
            type:Number,
            default:39.915
        },
        is_jlfb:{
            type:Boolean
        },
        is_jqfb:{
            type:Boolean
        },
        is_zdfb:{
            type:Boolean
        }
    },
    data:function(){
        return{
            mapStyle:{
                width:'100%',
                height:this.mapHeight +'px'
            },

            data_jlfb: {
                points:[]
            },
            data_jqfb:{
                points:[]
            },
            data_zdfb:{
                points:[]
            },
            marker_jlfb:[],
            marker_jqfb:[],
            marker_zdfb:[]
        }
    },

    methods: {
        backinitposition () {
            // console.log("I want back")
            this.mybdmap.centerAndZoom(new BMap.Point(this._point_x, this._point_y), 16);
        },
        createMap() {

            let userinfo = global.data.getUserInfo();
            let user_dept = userinfo.dept;
            // console.log(userinfo);
            //console.log(user_dept);

            let xyz = user_dept.location;
            let xyz_arr = xyz.split(",");
            let _point_x;
            let _point_y;
            if (xyz_arr && xyz_arr.length > 0) {
                _point_x = xyz_arr[0];
                _point_y = xyz_arr[1];
            }

            //
            let BMap = this.$BMap;
            let map = new BMap.Map("mybaidumap");
            this.mybdmap = map;
            this._point_x = _point_x;
            this._point_y = _point_y;
            //console.log(map);
            map.centerAndZoom(new BMap.Point(_point_x, _point_y), 18);
            var mapType = new BMap.MapTypeControl({mapTypes: [BMAP_NORMAL_MAP,BMAP_HYBRID_MAP]});
            map.addControl(mapType); 
            map.enableScrollWheelZoom();
            map.addControl(new BMap.NavigationControl());
            let _location = user_dept.xyz;
            let arr_loaction = _location ?  _location.split("|") : new Array();
            
            let point_arr = [];
            for (let laction= 0; laction < arr_loaction.length; laction++) {
                let locat_str = arr_loaction[laction]+"";
                let _arr = locat_str.split(",");
                point_arr.push(new BMap.Point(_arr[0],_arr[1]));
            }
            let polygon = new BMap.Polygon(point_arr, {strokeColor:"blue", strokeWeight:3, strokeOpacity:1, fillOpacity:0.1});  //
            map.addOverlay(polygon);   //
            map.setViewport(point_arr);
            this.map = map;
        },
        bindMarkerEvent(marker, type, value) {
            let that = this;
            const Bus = global.eventHub;

            if (type == "jlfb") {
                //

                marker.addEventListener("click", function(){
                    Bus.$emit('doto_show_contact', value);
                    Bus.$emit('detail_contact',value);
                });
                that.marker_jlfb.push(marker);
                //console.log("555555");
                that.marker_jlfb.remDub();
                //console.log("6666666");
            } else if (type == "jqfb") {
                //
                marker.addEventListener("click", function(){
                    // console.log(value);
                    Bus.$emit('doto_show_bjxq', value);
                    Bus.$emit('detail_bjxq',value);
                });
                that.marker_jqfb.push(marker);
                that.marker_jqfb.remDub();
            } else if (type == "zdfb") {
                //
                marker.addEventListener("click", function(){
                    // console.log(value);
                    Bus.$emit('doto_show_zdfb', value);
                    Bus.$emit('detail_zhandian',value);
                });
                that.marker_zdfb.push(marker);
                that.marker_zdfb.remDub();
            }
        },
        unShowMarker(type) {
            let that = this;
            if (type == "jlfb") {
                for(let marker of that.marker_jlfb){
                    marker.hide();
                }
            } else if (type == "jqfb") {
                for(let marker of that.marker_jqfb){
                    marker.hide();
                }
            } else if (type == "zdfb") {
                for(let marker of that.marker_zdfb){
                    marker.hide();
                }
            }
        },
        addMarker(point, src, type, value) {

            //
            var that=this;
            let BMap = this.$BMap;
            var convertor = new BMap.Convertor();
            var pointArr = [];
            pointArr.push(point);
            convertor.translate(pointArr, 3, 5, translateCallback);
            function translateCallback(data){
              if(data.status === 0) {
                    let marker = new BMap.Marker(data.points[0], {icon: new BMap.Icon(src, new BMap.Size(21,24))});
                    that.map.addOverlay(marker);
                    // console.log(222);
                    that.bindMarkerEvent(marker, type, value);
                }
            }
        },
        doto_jlfb() {
            //console.log(1111);
            let BMap = this.$BMap;

            let that = this;
            if (that.is_jlfb) {
                //
                that.$http.post(
                        global.config.weburl+'api/user/queryUserList.do',
                        {
                            token:global.data.getToken(),
                            type:"backUser",
                            auth:"authed",
                            sortby:"is_online desc, realname asc"
                        }).
                then(function(res){
                    //console.log(res);
                    var arr=res.body.data;
                    var local=[];
                    if(arr){
                        arr.forEach(function(value,index,array){
                            console.log("");
                            if(value.location){
                                local=value.location.split(",");
                            }
                            if(value.type==1){
                                that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingcha.png", "jlfb", value);
                            }else if(value.type==2){
                                that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/qungan.png", "jlfb", value);
                            }else{
                                that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/minjing.png", "jlfb", value);
                            }
                        });
                    }
                },function(error){

                });



                // console.log("");
                // that.addMarker(new BMap.Point(116.389226,39.917988), "./src/img/icon2.png","jlfb" , 2 );

            } else {
                //
                that.unShowMarker("jlfb");
            }
        },
        doto_jqfb() {

            let that = this;
            let BMap = that.$BMap;

            if (that.is_jqfb) {
    //             //
                that.$http.post(
                global.config.weburl+'api/alert/queryAlert.do',
                {
                    token:global.data.getToken(),
                    status:0,
                    page:1,
                    pageSize:10000
                }).
                then(function(res){
                    // console.log(res);
                    var arr=res.body.data;
                    var local=[]
                    if(arr){
                        arr.forEach(function(value,index,array){
                        // console.log("");
                        local=value.location.split(",");
                        if(value.type==1){
                            console.log(value);
                            console.log("==============" +local);
                            that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingqing1.png","jqfb" , value );
                        }else if(value.type==2){
                            console.log(2);
                            console.log("==============" +local);
                            that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingqing2.png","jqfb" , value );
                        }else{
                            console.log(3);
                            console.log("==============" +local);
                            that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingqing3.png","jqfb" , value );
                        }
                        
                     });
                    }
                    
                 },function(error){
                    
                 });

                // console.log("");
                // that.addMarker(new BMap.Point(116.394226,39.917988), "./src/img/icon/jingqing1.png","jqfb" , 0 );
            } else {
                //
                that.unShowMarker("jqfb");
            }
        },
        doto_zdfb() {
            let that = this;
            let BMap = this.$BMap;

            if (that.is_zdfb) {
                //
                that.$http.get(
                global.config.weburl+'api/entity/list.do',
                {
                    params:{token:global.data.getToken()}
                }).
                then(function(res){
                    // console.log("32312312312")
                    // console.log(res);
                    var arr=res.body.data;
                    var local=[];
                    if(arr){
                        arr.forEach(function(value,index,array){
                            if(value.location){
                                local=value.location.split(","); 
                                //console.log("");
                                if(value.type==1){
                                    that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingting1.png","zdfb" , value);
                                }else if(value.type==2){
                                     that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingting2.png","zdfb" , value);
                                }else{
                                    that.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingting3.png","zdfb" , value);
                                }
                            }
                            
                        
                     });
                    }
                 },function(error){
                    
                 }); 

            } else {
                //
                that.unShowMarker("zdfb");
            }
        },
        initMap (BMap) {
            let that = this;
            this.$BMap = BMap;
            //console.log(this.$BMap);
            this.createMap();


        },
        getMapScript () {
            //const ak = this._BMap().ak
            if (!global.BMap) {
                return new Promise((resolve, reject) => {
                    global._initBaiduMap = function () {
                        resolve(global.BMap)
                        global.document.body.removeChild($script)
                        global._initBaiduMap = null
                    }
                    const $script = document.createElement('script')
                    global.document.body.appendChild($script)
                    $script.src = '//api.map.baidu.com/api?v=2.0&ak=pYyi0ezfhpE9Exceq26QAcge&callback=_initBaiduMap';
                })
            }
            return Promise.resolve(global.BMap)
        }
    },

    created () {

        const {getMapScript, initMap} = this
        getMapScript().then(initMap);
        // console.log("map created");

    },
    mounted:function(){
        const that = this;
        const Bus = global.eventHub;



        Bus.$on('backinitposition', function() {
        //
            //console.log("obj");
            //that.isShow_bjxq = false;
            //console.log("2222");
            that.backinitposition();

        });


        Bus.$on('doto_jlfb', that.doto_jlfb);
        Bus.$on('doto_jqfb', that.doto_jqfb);
        Bus.$on('doto_zdfb', that.doto_zdfb);

        Bus.$on('doto_jqlb_refresh', function () {
            //
            //console.log("baidumap");
            that.doto_jqfb();
        });

        // 
    },
    beforeDestroy: function () {
        const Bus = global.eventHub;
        let that = this;
        //
        //console.log("beforeDestroy  baidumap");
        Bus.$off('doto_jlfb', this.doto_jlfb);
        Bus.$off('doto_jqfb', this.doto_jqfb);
        Bus.$off('doto_zdfb', this.doto_zdfb);
        Bus.$off('doto_jqlb_refresh', function () {
            //
            //console.log("beforeDestroy baidumap");
            that.doto_jqfb();
        });
        Bus.$off('backinitposition', function() {
        //
            //console.log("obj");
            //that.isShow_bjxq = false;
            //console.log("2222");
            that.backinitposition();

        });
    }

}
</script>


